import { ENDPOINT } from './constants';

export async function fetchCurrentPlot(): Promise<Response> {
  // Call the API endpoint
  return fetch(`${ENDPOINT}/plot/current`);
}

interface Series {
  name: string;
}

export async function fetchPauseControl(enabled: boolean): Promise<Response> {
  // Call the API endpoint
  const action = enabled ? 'enable' : 'disable';
  return fetch(`${ENDPOINT}/plot/pause/${action}`, {
    method: 'POST',
  });
}

export function processPlotData(result: any) {
  const data = result.data;
  const layout = result.layout;
  // We have to explicitly set the range of the yaxis
  // Check if data has a series called OHLC
  const range = [0, 1];
  if (data.some((series: Series) => series.name === 'OHLC')) {
    range[0] = Math.min(...(data.find((series: Series) => series.name === 'OHLC')! as any).low) * 0.995;
    range[1] = Math.max(...(data.find((series: Series) => series.name === 'OHLC')! as any).high) * 1.005;
    (layout as any).yaxis = {
      range: range,
      fixedrange: true,
      domain: [0.55, 1],
    };
  }
  (layout as any).height = window.innerHeight - 140;
  // For each shape of layoput
  (layout as any).shapes.forEach((shape: any) => {
    if (shape.fillcolor === 'LightSkyBlue') {
      shape.yref = 'y';
      shape.y0 = range[0];
      shape.y1 = range[1];
    }
  });
}

export function getPausedLayout() {
  return {
    title: null,
    margin: {t: 10, l:50, r:50, b:50},
    annotations: [
      {
        text: "Paused",
        xref: "paper",
        yref: "paper",
        x: 0.5,
        y: 0.5,
        showarrow: false,
        font: {size: 20},
      }
    ],
    height: window.innerHeight - 140,
  };
}