<template>
  <div>
    <v-toolbar>
      <v-btn @click="dryRun" class="mx-1">Dry Run</v-btn>
      <v-btn @click="clearLogs" class="mx-1">Clear</v-btn>
    </v-toolbar>

    <v-list>
      <v-list-item-group>
        <v-list-item v-for="log in logs" :key="log">
          <v-list-item-content>
            <v-list-item-title>{{ log }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script lang="ts">
import { Component, Vue } from 'vue-property-decorator'

import { fetchTraderClear, fetchTraderDryRun, fetchTraderLogs } from '@/utils/trader_utils'

@Component
export default class TraderStatus extends Vue {
  logs: string[] = [];

  mounted() {
    this.fetchLogs();
    setInterval(this.fetchLogs, 5000);
  }


  // Dummy method for Dry Run
  dryRun() {
    fetchTraderDryRun();
  }

  // Clear logs
  clearLogs() {
    fetchTraderClear();
  }

  private async fetchLogs() {
    try {
      const response = await fetchTraderLogs();
      if (response.status === 200) {
        const returned = await response.json();
        this.logs = returned.logs;
      } else {
        this.logs = ["Error fetching logs"];
      }
    } catch (error) {
      console.error(error);
    }
  }
}
</script>