import { ENDPOINT } from './constants';

export async function fetchCheckLogin(): Promise<Response> {
  // Call the API endpoint
  return fetch(`${ENDPOINT}/login/check`);
}

export async function fetchLogin(username: string, password: string, mfa: string | undefined = undefined): Promise<Response> {
  // Call the API endpoint
  return await fetch(`${ENDPOINT}/login/login`, {
    method: 'POST',
    headers: {
        'Content-Type': 'application/json'
    },
    body: JSON.stringify({ username, password, mfa })
  });
}