<template>
  <v-container fill-height fluid>
    <v-responsive align-center class="pa-2">
      <v-card class="mx-auto px-6 py-8" max-width="344">
        <v-alert
          v-if="lastErrorMessage"
          type="error"
          class="mb-4"
        >
          {{ lastErrorMessage }}
        </v-alert>

        <v-alert
          v-else-if="loginState === LoginState.LoggedIn"
          type="success"
          class="mb-4"
        >
          Logged In
        </v-alert>

        <v-alert
          v-else-if="loginState === LoginState.LoggedOut"
          type="warning"
          class="mb-4"
        >
          Logged Out
        </v-alert>

        <v-alert
          v-else-if="loginState === LoginState.Unknown"
          type="info"
          class="mb-4"
        >
          Unknown Login Status
        </v-alert>

        <v-alert
          v-else-if="loginState === LoginState.MFARequired"
          type="info"
          class="mb-4"
        >
          Multi-Factor Authentication required. Please check your device for a code.
        </v-alert>
        
        <v-form
          v-model="form"
          @submit.prevent="onSubmit"
        >
          <v-text-field
            v-model="username"
            :readonly="loading"
            :rules="[required]"
            class="mb-2"
            clearable
            label="Username"
          ></v-text-field>

          <v-text-field
            v-model="password"
            :readonly="loading"
            :rules="[required]"
            type="password"
            clearable
            label="Password"
            placeholder="Enter your password"
          ></v-text-field>

          <v-dialog v-model="showMfaDialog" max-width="290">
            <v-card>
              <v-card-title>Enter MFA Code</v-card-title>
              <v-card-text>
                <v-text-field
                  v-model="mfaCode"
                  label="MFA Code"
                  type="number"
                ></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-btn @click="showMfaDialog = false">Cancel</v-btn>
                <v-btn color="primary" @click="submitMfa">Submit</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <br>

          <v-btn
            :disabled="!form"
            :loading="loading"
            block
            color="success"
            size="large"
            type="submit"
            variant="elevated"
          >
            Sign In
          </v-btn>
        </v-form>
      </v-card>
    </v-responsive>
  </v-container>
</template>

<script lang="ts">
import { Component, Vue } from 'vue-property-decorator'

import { fetchCheckLogin, fetchLogin } from '@/utils/login_utils'

enum LoginState {
  LoggedIn,
  LoggedOut,
  MFARequired,
  Unknown,
}

@Component
export default class LoginManager extends Vue {
  LoginState = LoginState;

  private form = false;
  private username = "";
  private password = "";
  private loading = false;
  private showMfaDialog = false;
  private mfaCode = "";

  private loginState = LoginState.Unknown;

  private lastErrorMessage = "";
  private required = (value: string) => !!value || "This field is required.";

  mounted() {
    this.checkLogin();
  }

  private async checkLogin() {
    this.loading = true;

    try {
      const response = await fetchCheckLogin();
      const data = await response.json();

      if (data.result) {
        this.loginState = LoginState.LoggedIn;
      } else {
        this.loginState = LoginState.LoggedOut;
      }

    } catch (error) {
      console.error(error);
      this.lastErrorMessage = "Error checking login status."; // This message can be more specific based on the error details if desired.
    } finally {
      this.loading = false;
    }
  }

  private async onSubmit() {
    this.loading = true;
    const response = await fetchLogin(this.username, this.password);

    if (response.status === 401) {
      const data = await response.json();
      if (data.message === "MFA Required") {
        this.loginState = LoginState.MFARequired;
        this.showMfaDialog = true; // Show the MFA input dialog
      } else {
        this.loginState = LoginState.LoggedOut;
      }
    } else if (response.status === 200) {
      this.loginState = LoginState.LoggedIn;
    } else {
      this.loginState = LoginState.LoggedOut;
    }

    this.checkLogin();
  }

  private async submitMfa() {
    // Close the dialog
    this.showMfaDialog = false;

    // Now try logging in with the MFA code
    const response = await fetchLogin(this.username, this.password, this.mfaCode);
    if (response.status === 200) {
      this.loginState = LoginState.LoggedIn;
    } else {
      const data = await response.json();
      this.lastErrorMessage = data.message || 'Error logging in with MFA.';
      this.loginState = LoginState.LoggedOut;
    }

    this.checkLogin();
  }
}
</script>
