<template>
  <div>
    <v-toolbar class="plotly py-2" fixed top>
      <v-btn @click="togglePause" class="ma-1">
        {{ paused ? 'Resume' : 'Pause' }}
      </v-btn>
    </v-toolbar>
    <Plotly :data="data" :layout="layout" :display-mode-bar="true" fill-height class="plotly"></Plotly>
  </div>
</template>

<script lang="ts">
import { Component, Watch, Vue } from 'vue-property-decorator'
import { Plotly } from 'vue-plotly'

import { fetchCurrentPlot, processPlotData, fetchPauseControl, getPausedLayout } from '@/utils/plot_utils'

@Component({
  components: {
    Plotly,
  }
}) 
export default class RealTimePlot extends Vue {
  data = [];
  layout = {};
  paused = false;

  mounted() {
    this.fetchPlot();
    setInterval(this.fetchPlot, 5000);
  }

  private async fetchPlot() {
    try {
      const response = await fetchCurrentPlot();
      if (response.status === 200) {
        const returned = await response.json();
        this.paused = returned.paused;

        // Only update the Plotly data if not paused
        if (!this.paused) {
          const parsed = JSON.parse(returned.result);
          processPlotData(parsed);
          this.data = parsed.data;
          this.layout = parsed.layout;
        } else {
          this.data = [];
          this.layout = getPausedLayout();
        }
      } else {
        this.data = [];
        this.layout = {};
        this.paused = false;
      }
    } catch (error) {
      console.error(error);
    }
  }

  private async togglePause() {
    fetchPauseControl(!this.paused);
  }
}
</script>

<style scoped>
  /* Add style for Plotly */
  .plotly {
    width: 100%;
  }
</style>
