import { ENDPOINT } from './constants';

export async function fetchTraderClear(): Promise<Response> {
  // Call the API endpoint
  return fetch(`${ENDPOINT}/trader/clear`);
}

export async function fetchTraderDryRun(): Promise<Response> {
  // Call the API endpoint
  return fetch(`${ENDPOINT}/trader/dry-run`);
}

export async function fetchTraderLogs(): Promise<Response> {
  // Call the API endpoint
  return fetch(`${ENDPOINT}/trader/log`);
}